import {
  AddressValue,
  PostalCode,
  ProjectAddressId,
  ProjectId,
  Province,
} from '../value';

export class Address {
  private _projectAddressId: ProjectAddressId;
  private _projectId: ProjectId;
  private _projectPostalCode: PostalCode;
  private _projectProvince: Province;
  private _projectAddress: AddressValue;

  constructor(args: {
    projectAddressId: ProjectAddressId;
    projectId: ProjectId;
    projectPostalCode: PostalCode;
    projectProvince: Province;
    projectAddress: AddressValue;
  }) {
    this._projectAddressId = args.projectAddressId;
    this._projectId = args.projectId;
    this._projectPostalCode = args.projectPostalCode;
    this._projectProvince = args.projectProvince;
    this._projectAddress = args.projectAddress;
  }

  public get projectAddressId() {
    return this._projectAddressId;
  }
  public get projectId() {
    return this._projectId;
  }
  public get projectPostalCode() {
    return this._projectPostalCode;
  }
  public get projectProvince() {
    return this._projectProvince;
  }
  public get projectAddress() {
    return this._projectAddress;
  }

  update(args: {
    projectPostalCode?: PostalCode;
    projectProvince?: Province;
    projectAddress?: AddressValue;
  }) {
    return new UpdateAddress({
      projectAddressId: this.projectAddressId,
      projectId: this.projectId,
      projectPostalCode: args.projectPostalCode,
      projectProvince: args.projectProvince,
      projectAddress: args.projectAddress,
    });
  }
}

export class UpdateAddress {
  readonly projectAddressId: ProjectAddressId;
  readonly projectId: ProjectId;
  readonly projectPostalCode?: PostalCode;
  readonly projectProvince?: Province;
  readonly projectAddress?: AddressValue;

  constructor(args: {
    projectAddressId: ProjectAddressId;
    projectId: ProjectId;
    projectPostalCode?: PostalCode;
    projectProvince?: Province;
    projectAddress?: AddressValue;
  }) {
    this.projectAddressId = args.projectAddressId;
    this.projectId = args.projectId;
    this.projectPostalCode = args.projectPostalCode;
    this.projectProvince = args.projectProvince;
    this.projectAddress = args.projectAddress;
  }
}
